.App {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem 1rem;
  min-height: 100vh;
}

/* Header */
.header {
  text-align: center;
  margin-bottom: 3rem;
  border-bottom: 2px solid var(--caramel);
  padding-bottom: 1.5rem;
}

.header h1 {
  font-size: 3rem;
  font-weight: 700;
  margin: 0;
  color: var(--coffee-bean);
  letter-spacing: -0.02em;
}

/* Main content */
.main-content {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

/* Sections */
.section {
  margin-bottom: 2rem;
}

.section h2 {
  font-size: 2.2rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  color: var(--coffee-bean);
  border-bottom: 1px solid var(--latte);
  padding-bottom: 0.5rem;
}

.section p {
  font-size: 1.1rem;
  color: var(--coffee-brown);
  line-height: 1.7;
  margin-bottom: 2rem;
}

/* Projects content */
.projects-content {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  margin-top: 1rem;
}



/* Coursework content */
.coursework-content {
  margin-top: 1rem;
}

.coursework-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1rem;
}

.coursework-item {
  background-color: var(--cream);
  border: 1px solid var(--latte);
  border-radius: 8px;
  padding: 1rem;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(166, 123, 91, 0.06);
}

.coursework-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background-color: var(--caramel);
}

.coursework-item:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(166, 123, 91, 0.12);
  border-color: var(--caramel);
}

.coursework-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 0.5rem;
}

.coursework-header h3 {
  margin: 0;
  color: var(--espresso);
  font-size: 1rem;
  font-weight: 600;
  line-height: 1.3;
}

.coursework-date {
  color: var(--mocha);
  font-size: 0.8rem;
  font-weight: 500;
  font-style: italic;
  white-space: nowrap;
  margin-left: 0.5rem;
}

.coursework-skills {
  color: var(--mocha);
  font-size: 0.8rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
  line-height: 1.4;
}

.coursework-description {
  color: var(--coffee-brown);
  margin: 0;
  line-height: 1.5;
  font-size: 0.85rem;
}

/* Responsive design */
@media (max-width: 768px) {
  .App {
    padding: 1rem;
  }
  
  .header h1 {
    font-size: 2.5rem;
  }
  
  .section h2 {
    font-size: 1.8rem;
  }
}

.collapsible {
  cursor: pointer;
  user-select: none;
  display: flex;
  align-items: center;
  gap: 0.15rem;
  font-size: 1.05rem;
}

.chevron {
  font-size: 0.48em;
  color: var(--mocha);
  transition: transform 0.2s, color 0.2s;
  display: inline-block;
  margin-left: 0.06em;
  font-weight: 400;
  opacity: 0.5;
  line-height: 1;
}

.chevron.open {
  transform: rotate(180deg);
  opacity: 0.7;
  color: var(--cappuccino);
}

/* Theme switcher: top right on desktop, bottom right on mobile */
.theme-switcher {
  position: fixed;
  top: 1rem;
  right: 1rem;
  z-index: 1000;
}

@media (max-width: 768px) {
  .theme-switcher {
    top: auto;
    bottom: 1.2rem;
    right: 1.2rem;
    left: auto;
  }
} 