.theme-switcher {
  position: fixed;
  top: 1rem;
  right: 1rem;
  z-index: 1000;
}

.theme-switcher.mobile {
  top: auto;
  bottom: 1.2rem;
  right: 1.2rem;
  left: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.theme-fab {
  background: var(--cappuccino);
  color: var(--espresso);
  border: none;
  border-radius: 50%;
  width: 44px;
  height: 44px;
  box-shadow: 0 2px 8px rgba(139, 115, 85, 0.15);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.3rem;
  cursor: pointer;
  transition: background 0.2s, box-shadow 0.2s;
  margin-bottom: 0.2rem;
}

.theme-fab:active, .theme-fab:focus {
  background: var(--mocha);
  outline: none;
}

.theme-fab-icon {
  pointer-events: none;
}

.theme-options-popup {
  background: var(--cream);
  border: 1px solid var(--latte);
  border-radius: 10px;
  box-shadow: 0 4px 16px rgba(139, 115, 85, 0.13);
  padding: 0.3rem 0.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
  margin-top: 0.2rem;
}

.theme-options {
  display: flex;
  background-color: var(--cream);
  border: 2px solid var(--cappuccino);
  border-radius: 12px;
  padding: 0.25rem;
  gap: 0.25rem;
  box-shadow: 0 4px 12px rgba(139, 115, 85, 0.15);
}

.theme-option {
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  border-radius: 8px;
  padding: 0.75rem 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 60px;
  color: var(--coffee-brown);
  font-family: 'Inter', sans-serif;
  font-size: 0.9rem;
  font-weight: 500;
}

.theme-option:hover {
  background-color: var(--latte);
  transform: translateY(-1px);
}

.theme-option.active {
  background-color: var(--cappuccino);
  color: var(--espresso);
  box-shadow: 0 2px 8px rgba(139, 115, 85, 0.2);
}

.theme-label {
  font-size: 0.9rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Responsive design */
@media (max-width: 768px) {
  .theme-switcher {
    top: auto;
    bottom: 1.2rem;
    right: 1.2rem;
    left: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  .theme-options {
    display: none;
  }
  
  .theme-options {
    padding: 0.2rem;
    gap: 0.2rem;
  }
  
  .theme-option {
    padding: 0.6rem 0.8rem;
    min-width: 50px;
  }
  
  .theme-label {
    font-size: 0.8rem;
  }
} 