* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--flat-white);
  color: var(--coffee-brown);
  line-height: 1.6;
  transition: background-color 0.3s ease, color 0.3s ease;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Light coffee theme colors (default) */
:root {
  --flat-white: #F8F4E6;
  --coffee-brown: #3D2B1F;
  --latte: #E8D5B7;
  --espresso: #2C1810;
  --cappuccino: #D4B08C;
  --mocha: #A67B5B;
  --cream: #FEFCF8;
  --steam: #F5F0E8;
  --caramel: #C4A484;
  --coffee-bean: #5D4037;
}

/* Dark coffee theme colors */
[data-theme="dark"] {
  --flat-white: #1A0F0A;
  --coffee-brown: #E6D7C3;
  --latte: #3C2A1A;
  --espresso: #F5F5DC;
  --cappuccino: #8B7355;
  --mocha: #D4C4A8;
  --cream: #2C1810;
  --steam: #3C2A1A;
  --caramel: #D4C4A8;
  --coffee-bean: #E6D7C3;
}

/* System preference for automatic mode */
@media (prefers-color-scheme: dark) {
  [data-theme="auto"] {
    --flat-white: #1A0F0A;
    --coffee-brown: #E6D7C3;
    --latte: #3C2A1A;
    --espresso: #F5F5DC;
    --cappuccino: #8B7355;
    --mocha: #D4C4A8;
    --cream: #2C1810;
    --steam: #3C2A1A;
    --caramel: #D4C4A8;
    --coffee-bean: #E6D7C3;
  }
} 